@import "../../global.scss";

.topbar {
    width: 100%;
    height: 20%;
    background-color: black;
    color: $maincolor;
    // position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: center;
    
        .left{
            display: flex;
            align-items: center;
            
            .itemcontainer{
                display: flex;
                align-items: center;
                flex-direction: column;

                @include mobile {
                    display: none;
                }

                img {
                    width: 50px;
                    position: relative;
                    margin: 0;
                    padding: 0;
                    overflow: inherit;
                  }
            }
        }

        .right{
            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                span{
                    width: 100%;
                    height: 3px;
                    background-color: $maincolor;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }
    &.active {
        background-color: $maincolor;
        color: white;

        .hamburger{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child {
                    background-color: white;
                    transform: rotate(-45deg);
                }
                
            }
        }
    }
}